[class*='mzb'] {

	h1,
	h2,
	h3 {
		margin: 0;
	}
}

button.mzb-reset-button {
	top: 2px;
}

.magazine-blocks-hide-on-desktop {
	background: repeating-linear-gradient(125deg, rgba(0, 0, 0, 0.05), rgba(0, 0, 0, 0.05) 1px, transparent 2px, transparent 9px);
	filter: opacity(0.4) saturate(0);
	border: 1px solid rgba(0, 0, 0, 0.02);
}

.mzb-featured-posts,
.mzb-post-list {
	.mzb-post-heading h2 {
		font-size: 1.125rem;
		font-weight: 500;
	}
}

.mzb-grid-module {

	.mzb-post-author a,
	.mzb-post-date a {
		font-size: 13px;
	}
}

.mzb-post-list {
	.mzb-post-date a {
		font-size: 13px;
	}
}

.mzb-tab-post {
	.mzb-post-title a {
		font-size: 16px;
	}

	.mzb-post-date a {
		font-size: 13px;
	}
}

.mzb-featured-categories {
	.mzb-post:nth-child(2) {
		.mzb-post-content {
			width: calc(100% - 50px);
		}

		.mzb-post-title a {
			font-size: 18px;
		}
	}

	.mzb-post-title a {
		font-size: 16px;
	}
}

.comments-link {
	font-size: 14px;
}

.mzb-tab-post {
	.mzb-tab-title {
		font-size: 18px;
	}
}

.mzb-grid-module,
.mzb-featured-posts,
.mzb-featured-categories, .mzb-slider{
	.mzb-post-heading h2 {
		font-size: 24px;
	}
}

.mzb-news-ticker-box {
	.editor-styles-wrapper li {
		list-style: 1.4;
	}

	ul {
		margin: 0;
	}
}

.mzb-post-date {
	svg {
		height: 14px;
		width: 14px;
	}
}

html :where(.editor-styles-wrapper) {
	a {
		color: #111111;
		text-decoration: none;
	}
}

.editor-styles-wrapper .edit-post-visual-editor__post-title-wrapper>*,
.editor-styles-wrapper .block-editor-block-list__layout.is-root-container>* {
	max-width: unset;
}

.editor-styles-wrapper .wp-block {
	max-width: 100%;
}

[data-cols="2"] {
	svg {
		width: 100px;
		height: 45px;
	}
}

.magazine-blocks .block-editor-url-input,
.magazine-blocks-popover-drawer .block-editor-url-input {
	min-width: auto;
}

.block-editor-block-card {
	.block-editor-block-icon {
		padding-top: 8px;
	}
}
