.mzb-media {
	label {
		color: #222222;
		font-size: 14px;
		font-weight: 600;
		width: 100%;
	}

	.mzb-media-head {
		display: flex;
		align-items: center;

		.mzb-media-types {
			display: flex;
			gap: 8px;

			.mzb-media-type {
				all: unset;
				padding: 2px 4px;
				background-color: #fafbff;
				border-radius: 2px;
				color: #64748b;
				font-size: 13px;
				font-weight: 600;
				cursor: pointer;
				border: 1px solid #e2e8f0;

				&.is-active {
					background-color: #e1eaff;
					color: #690aa0;
					position: unset;
					border: 1px solid #7ea6ff;
				}

				&::after {
					all: unset;
				}
			}
		}
	}

	&-body {
		margin-top: 6px;
	}

	.mzb-media-placeholder {
		cursor: pointer;

		img {
			width: 100%;
			height: 155px;
			object-fit: cover;
			margin-bottom: 16px;
		}
	}

	.mzb-media-external {
		display: flex;
		flex-direction: column;
		gap: 12px;
	}
}

.components-button {
	&.mzb-media-button {
		background: #690aa0;
		color: #fafbff;
		border-radius: 2px;
		font-weight: 600;
		cursor: pointer;
		padding: 12px 20px;
		border: 1px solid #690aa0;
		display: inline-flex;

		&:hover {
			color: #fafbff;
		}

		svg {
			height: 20px;
			width: 20px;
			fill: #fafbff;
		}

		.mzb-media-button-text {
			padding-left: 12px;
		}
	}

	&.mzb-media-delete {
		all: unset;
		padding-left: 18px;

		svg {
			height: 24px;
			width: 24px;
			fill: #df4a6e;
			cursor: pointer;
		}
	}
}