.components-placeholder.mzb-section-preset {
	background: #fafbff !important;
	padding: 32px 20px;
	box-shadow: none !important;

	.components-placeholder__label {
		margin: 0 auto;
		font-weight: 600;
	}

	.components-placeholder__fieldset {
		justify-content: center;
		margin-top: 32px;
	}

	.mzb-section-preset-group {
		max-width: 800px;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 16px;
	}

	.mzb-section-preset-btn {
		width: 105px;
		height: 50px;
		padding: 0 !important;
		background-color: transparent !important;
		display: inline-flex;
		cursor: pointer;
		outline: none;
		border: none;
		box-shadow: none !important;

		&:hover {
			> i {
				background-color: #e1eaff;
			}
		}

		> i {
			height: 50px;
			border-left: 1px solid #fafbff;
			background-color: #e2e8f0;

			&:first-child {
				border: none;
			}
		}
	}

	.mzb-section-preset-import-btn {
		width: 100%;
		text-align: center;
		margin-top: 32px;
		cursor: pointer;

		> button {
			background-color: #690aa0 !important;
			padding: 8px 16px !important;
			font-size: 14px;
			font-weight: 600;
			line-height: 21px;
			color: #fff;
			border: none;
			box-shadow: none;
		}
	}
}

.mzb-section-inner {
	flex-wrap: wrap;
}

[data-type="magazine-blocks/column"] {
	margin: 0 !important;

	.mzb-column {
		width: 100% !important;
	}

	&:not(.is-selected) {
		> .mzb-column {
			> .mzb-column-inner {
				> .block-list-appender:only-child {
					pointer-events: none;
					outline: #000 dashed 1px;

					> .components-dropdown {
						visibility: hidden;
					}
				}
			}
		}
	}
}
