.chakra-portal {

	.chakra-button__group {
		padding: 16px;
		overflow-y: scroll;
		background: white;

		&::-webkit-scrollbar {
			width: 4px;
		}

		&::-webkit-scrollbar-track-piece {
			background: white;
		}

		&::-webkit-scrollbar-thumb {
			background: #e0e0e0;
		}
	}

	.chakra-stack {
		margin-bottom: 0;

		label {
			margin-bottom: 0;
			margin-inline-end: 0;
			padding: 16px 16px 0 16px;
			font-size: 13px;
			font-weight: 600;
			color: #222222;
		}
	}

	.chakra-select__wrapper {
		padding: 16px;

		.chakra-select {
			padding: 0 16px;
		}

		svg {
			position: absolute;
			left: -10px;
		}
	}
}

.chakra-button {
	.magazine-blocks-icon {
		fill: #000000 !important;
	}
}

.chakra-tabs {
	.chakra-tabs__tab {
		font-size: 13px;
		color: #222222;
		font-weight: 600;
		line-height: 16px;
		padding-top: 0;
		
		&:active{
			background: none;
		}
		
		&[aria-selected="true"] {
			border-color: #7E36F4;
		}
	}
}

.components-panel {
	.chakra-tabs {
		.chakra-tabs__tablist {
			border: 1px solid #A0AEC0;
			padding: 4px;
			border-radius: 2px;
		}

		.chakra-tabs__tab {
			font-size: 12px;
			color: #222222;
			font-weight: 600;
			line-height: 16px;
			flex: 1 1 0%;
			padding: 8px 16px;

			&[aria-selected="true"] {
				border-color: #7E36F4;
				color: #fff;
				background: #7E36F4;
				border-radius: 2px;
			}
		}
	}
}
