.mzb-input {
	display: flex;
	align-items: center;

	&.mzb-input-top {
		flex-direction: column;
		align-items: flex-start;
	}

	.mzb-control-head {
		margin-right: 8px;
		margin-bottom: 8px;
	}

	.mzb-control-body {
		align-items: center;
		box-sizing: border-box;
		border-radius: inherit;
		display: flex;
		flex: 1 1 0;
		position: relative;
		background-color: #fff;
		width: 100%;
		margin-bottom: 8px;

		input {
			width: 100%;
			padding: 10px 16px;
			background-color: transparent;
			box-sizing: border-box;
			border: 1px solid #e2e8f0 !important;
			display: block;
			outline: none;
			min-height: 30px;
			box-shadow: none !important;
			color: #64748b;
			font-size: 13px;
			font-weight: 600;
		}

		.components-base-control__field {
			margin: 0 !important;
		}

		.components-base-control {
			width: 100%;
		}
	}
}
