[class*='mzb'] {
	--wp-admin-theme-color: #690aa0;

	* {
		box-sizing: border-box;
	}
}

.block-editor-block-icon {
	.mzb-icon:not(.mzb-column-block) path {
		fill: #690aa0 !important;
	}
}

.mzb-library-button-wrapper {
	margin: 0 5px;
}

.mzb-library-button {
	all: unset;
	background-color: #690aa0;
	transition: all 200ms;
	border-radius: 2px;
	padding: 10px 15px !important;
	cursor: pointer;
	font-size: 14px;
	color: #fcfcfc;
	display: flex;
	align-items: center;
	height: unset !important;
	white-space: nowrap;
	margin: 0 10px;

	&:hover,
	&:focus,
	&:active {
		background-color: #690aa0;
		color: #fff !important;
	}
}

.mzb-control {

	ul,
	li {
		margin: 0;
	}
}

.mzb-control-label {
	display: inline-block;
	font-size: 13px;
	font-weight: 600;
	color: #64748b;
	line-height: 17px;
}

.mzb-icon {
	fill: #64748b;
	height: 18px;
	width: 18px;
}

.mzb-control input[type='text'] {
	border: 1px solid #e2e8f0;
	font-weight: 600;
	width: 100%;
	color: #64748b;
	font-size: 12px;
	padding: 10px 16px;
}

.mzb-device-selector {
	padding: 6px 0 !important;
	display: flex;

	.mzb-device {
		all: unset;
		cursor: pointer;
		padding: 4px;
		width: 16px;
		height: 16px;
		border-radius: 2px;
		text-align: center;
		display: inline-flex;
		align-items: center;
		justify-content: center;

		svg {
			height: 13px;
			width: 13px;
		}

		&.active {
			background: #e1eaff;

			svg {
				fill: #690aa0;
			}
		}
	}
}

.mzb-textarea {
	.components-base-control__label {
		font-size: 13px;
		font-weight: 600;
		color: #64748b;
		line-height: 17px;
	}

	.components-textarea-control__input {
		border: 1px solid #e2e8f0;
		font-weight: 600;
		width: 100%;
		color: #64748b;
		font-size: 12px;
		padding: 10px 16px;
	}
}

.mzb-device-selector+.mzb-units {
	margin-left: auto;
}

.mzb-units {
	.components-popover__content {
		padding: 0 !important;
	}

	button:focus {
		outline: 0 !important;
		box-shadow: none !important;
	}

	.mzb-units-btn {
		border: 1px solid #7ea6ff;
		background: #e1eaff;
		text-transform: uppercase;
		font-weight: 600;
		font-size: 10px;
		color: #690aa0;
		border-radius: 2px;
		width: 30px;
		height: 21px;
		cursor: pointer;
		padding: 0;
		justify-content: center;
	}

	.mzb-units-menu {
		button {
			background: #fff;
			color: #64748b;
			text-transform: uppercase;
			font-weight: 600;
			font-size: 10px;
			border-radius: 2px;
			width: 30px;
			height: 21px;
			padding: 0;
			cursor: pointer;
			justify-content: center;
		}
	}
}

.mzb-post,
.mzb-category {
	a {
		pointer-events: none !important;
	}
}

.magazine-blocks {
	.components-panel {
		z-index: unset;
		&__body {
			padding: 0;

			&.is-opened {
				padding: 16px;
			}
		}
	}
}
